import React, { useCallback, useEffect, useRef } from 'react'
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalBody,
  ModalCloseButton,
} from "@chakra-ui/react"
import LiteYouTubeEmbed from 'react-lite-youtube-embed';
import 'react-lite-youtube-embed/dist/LiteYouTubeEmbed.css'

import { responsive } from '../components/ThemeProvider/theme'

import { trackVideo } from '../utils/trackEvent';

// import Box from '../components/Box'
const progress = [10, 25, 50, 75, 90]

const VideoModal = ({ isOpen, onClose }) => {
  const ref = useRef()
  const timer = useRef()
  const loadIframAPI = useCallback(() => {
    const scriptID = 'iframe-api'
    let player;
    let tracked = {}
    const handleStateChange = (e) => {
      if (e.data === window.YT.PlayerState.PLAYING) {
        const playerTotalTime = player.getDuration();
        timer.current = setInterval(() => {
          const playerCurrentTime = player.getCurrentTime();

          const playerProgress = (playerCurrentTime / playerTotalTime) * 100;
          progress.every((p) => {
            if (tracked[p]) return true
            if (playerProgress >= p) {
              trackVideo(p)
              tracked[p] = true
            }
            return false
          })
        }, 500);
      } else {
        clearInterval(timer.current);
      }
    }

    const init = () => {
      const iframeRef = ref.current.querySelector('.yt-iframe')
      player = new window.YT.Player(iframeRef, {
        events: {
          onStateChange: handleStateChange
        }
      });
    }

    if (document.getElementById(scriptID)) {
      setTimeout(init)
    } else {
      const tag = document.createElement('script');
      tag.id = scriptID;
      tag.src = 'https://www.youtube.com/iframe_api';
      const firstScriptTag = document.getElementsByTagName('script')[0];
      firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
      window.onYouTubeIframeAPIReady = init
    }

  }, [])
  useEffect(() => {
    if (isOpen) {
      setTimeout(() => {
        ref.current?.querySelector('.lty-playbtn')?.click()
      })
    } else {
      if (timer.current) clearInterval(timer.current);
    }
  }, [isOpen])
  return (
    <Modal isCentered isOpen={isOpen} onClose={onClose} size="xl" autoFocus={false}>
      <ModalOverlay />
      <ModalContent>
        <ModalCloseButton />
        <ModalBody px={responsive('1em', '3em')} py="2em" ref={ref}>
          <LiteYouTubeEmbed
            id="GmIcwwmwA-s" // 影片ID
            iframeClass="yt-iframe"
            params="enablejsapi=1"
            title="YouTube Embed" // a11y, always provide a title for iFrames: https://dequeuniversity.com/tips/provide-iframe-titles Help the web be accessible ;)
            cookie={process.env.NODE_ENV === 'production'}
            onIframeAdded={loadIframAPI}
          />
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}

export default VideoModal
